.hover {
     border-radius: 8px;
     background-color: rgba(0, 0, 0, .1);
 }

.MessageBox {
    display: flex;
    flex-flow: column;
    overflow-y: scroll;
    overflow-x: hidden;
    width: 510px;
    padding-left: 5px;
    height: calc(100vh - 103px - 60px);
    .message {
        cursor: pointer;
        display: flex;
        flex-flow: column;
        max-width: calc(50% - 18px);
        border-radius: 10px;
        padding: 10px 5px;
        background-color: rgba(238, 240, 253, .9);

        .date {
            font-size: 12px;
            color: gray;
            user-select: none;
        }

        .avatar {
            border-radius: 50%;
            width: 40px;
            height: 40px;
        }
        .text {
            height: 14px;
            width: 435px;
        }
        .author {
            max-width: 100%;
            color: rgb(84,110,230)
        }

        .bottomLine {
            display: flex;
            border-top: 1px solid lightgrey;
            margin-top: 3px;
            justify-content: space-around;
            .button {
                margin-top: 5px;
                color: rgba(0, 0, 0, 0.5);
                padding: 5px;
                &:hover {
                    border-radius: 50%;
                    background-color: rgba(0, 0, 0, 0.2);
                }
            }
        }
    }
    .reverse {
        text-align: end;
        flex-direction: row-reverse;
    }
}