$lineHeight: 56px;
$lineFontSize: 18px;
.Input {
    width: 550px;
    outline: none;
    padding: 0 8px 0 8px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    margin-bottom: 8px;
    border-radius: 4px;
    height: $lineHeight;
    font-size: $lineFontSize;
    cursor: pointer;
    background-color: initial;
    .text {
        position: relative;
        top: ($lineHeight - $lineFontSize) / 2;
    }
   &:hover {
     border-color: rgba(0, 0, 0, .7);
   }
}
.label {
     position: relative;
     left: 10px;
     pointer-events: none;
     color: grey;
 }