.VideoWindow {
    position: fixed;
    width: 100vw;
    height: 100vw;
    left: 0;
    top: 0;
    z-index: 1005;
}
.VideoBlock {
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vw;
    position: fixed;
}
.Video {
    top: 0;
    left: 0;
    position: fixed;
    display: none;
    width: 100vw;
    height: 100vh;
}
.TopController {
    width: 100vw;
    background-color: rgba(0, 0, 0, .9);
    padding: 10px 4px;
    position: fixed;
    z-index: 1006;
    .name {
        color: white;
        font-size: 22px;
        height: 20px;
        display: inline-block;
        padding-left: 10px;
        width: 50vw;
        overflow: hidden;
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }
}
.BottomController {
    position: fixed;
    z-index: 1006;
    height: 20px;
    bottom: 0;
    width: 100vw;
    background-color: rgba(0, 0, 0, .7);
    padding: 10px 4px;
    .progressBlock {
        position: absolute;
        border-radius: 4px;
        left: 0;
        top: -5px;
        height: 5px;
        cursor: pointer;
        background-color: grey;
        width: 100vw;
        .progressLine {
            width: 0;
            border-radius: 4px;
            background-color: rgb(169, 112, 255);
            height: 5px;
            cursor: pointer;
        }
    }
    .settingIcon {
        width: 25px;
        display: inline-block;
        position: relative;
        left: calc(100vw - 100px);
        cursor: pointer;
    }
}
.ButtonsLine {
    display: inline-block;
    z-index: 1001;
    position: absolute;
    left: calc(100vw - 70px);
    cursor: pointer;
    .message {
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        position: absolute;
        padding: 8px;
        left: -80px;
        color: white;
        height: 18px;
        font-size: 18px;
        background-color: grey;
        top: -50px;
        border-radius: 4px;
    }
}