html{
    --left_menu_width: 210px;
    --width_main_part:775px;
    --header_height:48px;
    --left_div_to_survey_lines_demo_with_minus:-10.5px;
    --width_div_to_survey_lines_demo:504px;
    --left_div_to_survey_lines_demo:10px;
    --width_left_column:242px;
    --width_right_column:530px;
    --width_input_offer_news:525px;
    --button_play_video_for_post_video_width:90px;
}

@media(max-width: 2560px){
    html{
        --interface_left: 768px;
    }
}
@media(max-width: 1920px){
    html{
        --interface_left: 448px;
    }
}
@media(max-width: 1440px){
    html{
        --interface_left:208px;
    }
}
@media(max-width: 1280px){
    html{
        --width_main_part:775px;
        --interface_left:128px;
    }
}
@media(max-width: 1024px){
    html{
        --width_main_part:775px;
        --interface_left:0px;
    }
}

.header{
    z-index: 991;
    position: fixed;
    background-color: #e3f3ff;
    display: block;
    height: var(--header_height);
    width: 101vw;
    left: -1%;
    top:-1%;
}
.HeaderData{
    position: relative;
    left: calc(var(--interface_left) + 1% + 13px);
    width: calc( var(--left_menu_width) + var(--width_main_part) + 5px);
    letter-spacing: -0.36rem;
}
.div_button_hey{
    letter-spacing:normal;
    display: inline-block;
    left: 5px;
    position: relative;
    width: 60px;
    height: 35px;
    top:4px;
}
.button_hey {
    outline:none;
    background-color: #e3f3ff;
    display: inline-block;
    position: relative;
    width: 60px;
    height: 35px;
    text-align: left;
    cursor: pointer;
    transition: opacity 0.2s;
    font-size: 35px;
    padding-top: 3px;
}
.exit_btn{
    letter-spacing:normal;
    cursor: pointer;
    display: inline-block;
    width: 35px;
    height: 34px;
    position: relative;
    top: 12px;
    left: 562px;
    outline: none;
    outline: 0;
    border-width: 0;
    padding: 0;
}
.ExitButtonInHeader {
    position: relative;
    display: inline-block;
    width: 40px;
}
.exit_window {
    letter-spacing: normal;
    width:100%;
    height:100%;
    background-color:rgba(0,0,0,0.1);
    position:fixed;
    left:0;
    top:0;
    z-index:998;
}
.d2 {
    background-color:#fff;
    width:550px;
    height:600px;
    position:fixed;
    left:calc(50% - 275px);
    top:calc(50% - 300px);
    border-radius:15%;
    display:block;
}
.d3 {
    position: absolute;
    left:calc(50% - 306px);
    top:17%;
    width: 612px;
    font-size: 30px;
    text-align:center;
}
.d4 {
    cursor:pointer;
    position:absolute;
    top:50%;
    left:20%;
    width:120px;
    height:120px;
    border-radius: 15%;
    background-color:green;
    color:#fff;
    text-align:center;
    font-size:42px;
    font-family:Arial;
}
.d5 {
    cursor:pointer;
    position:absolute;
    top:50%;
    left:calc(10% + 240px);
    width:120px;
    height:120px;
    border-radius: 15%;
    background-color:#db1f1f;
    color:#fff;
    text-align:center;
    font-size:42px;
    font-family:Arial;
}
.exit_btn:hover{
    background-color: rgba(0, 0, 0, 0.2);
    border-radius: 4px;
}
.noselect {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.MusicInSiteTopPanel{
    letter-spacing:normal;
    height: 35px;
    z-index: 990;
    position: relative;
    width: 340px;
    top: 1px;
    display: inline-block;
}
.MusicLineInSiteMusic{
    position: relative;
    height: 35px;
    font-size: 29px;
    background-color: lightgray;
    border-radius: 4px;
}

/* Элементы */
    /*ButtonRepeat*/
    .BlockToButtonRepeat{
        cursor: pointer;
        display: inline-block;
        top:0;
        position: relative;
    }
    .imgButtonRepeat{
        top: -3px;
        position: relative;
        width: 30px;
        height: 30px;
        cursor: pointer; 
    }
    /*ButtonPlayOrPause*/
    .BlockToButtonPlayOrPause{
        cursor: pointer;
        height: 24px;
        display: inline-block;
        top:0;
        position: relative;
    }
    .imgButtonPlayOrPause{
        width: 30px;
        height: 30px;
        position: relative;
        top:0;
        cursor: pointer;
    }
    /*MusicNameInLineInSiteMusic*/
    .MusicNameInLineInSiteMusic{
        top:-8px;
        height: 24px;
        overflow:hidden;
        text-overflow: clip;
        position: relative;
        display: inline-block;
        width: 236px;
    }
    .ButtonPlayOrPauseSong {
        width: 30px;
        height: 24px;
    }
    .MusicNameInLineInSiteMusic.Empty {
        width: 340px;
    }
    .MusicNameInLineInSiteMusicInfinitiveLine {
        font-size: 20px;
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        pointer-events: none;
    }
    .MusicNameInLineInSiteMusicInfinitiveLine:empty::before {
        font-size: 20px;
        content: attr(placeholder);
        display: inline-block;
        color:gray;
    }
    .MusicNameInLineInSiteMusicInfinitiveLineEmpty {
        font-size: 20px;
        display: inline-block;
        color:gray;
        text-align: center;
        width: 340px;
        position: relative;
        bottom: 3px;
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }
    .MusicSearchLineData:empty::before {
        font-size: 20px;
        content: attr(data-placeholder);
        display: inline-block;
        color:gray;
    }
    .Music {
        z-index: 1001;
        border-radius: 6px;
        border:rgba(0, 0, 0, 0.2) 1px solid;
        background-color: #e3f3ff;
        display: block;
        min-height: 300px;
        width: 600px;
        max-height: 600px;
        right: 0px;
        position: absolute;
        overflow: scroll;
        overflow-x: hidden;
    }
    .Music::-webkit-scrollbar {
        width: 8px;
        border-radius: 6px;
    }
    .Music::-webkit-scrollbar-thumb {
        border-radius: 10px;
        box-shadow: inset 0 0 6px rgba(0,0,0,.3);
        background-color: #fff;
        border: 1px solid #000;
      }
    .InfiniteInput {
        background-color: #fff;
        top: 8px;
        left:20px;
        overflow:hidden;
        text-overflow: clip;
        position: relative;
        display: inline-block;
        width: 560px;
        height: 40px;
        border-radius: 4px;
    }
    .InfiniteInputData {
        outline: none;
        border: none;
        height: 29px;
        position: relative;
        width: 24000px;
        font-size: 20px;
        top:8px;
    }
    .EmptySearchResult {
        top: 70px;
        text-align: center;
        position: relative;
        font-size: 26px;
    }
    .MusicListMusicDiv {
        position: relative;
        top: 30px;
        border-top: 1px solid gray;
        height: 50px;
    }
    .MusicListMusicLine {
        position: relative;
        top: 13px;
    }
    .SongNameInMusicListMusicLine {
        width: 540px;
        left: 10px;
        height: 20px;
        top: -4px;
        font-size: 20px;
        overflow: hidden;
        text-overflow: ellipsis;
        display: inline-block;
        position: relative;
    }
    .SongInSiteProgressContainer {
        border-radius: 6px;
        display: block;
        top: 25px;
        left: 30px;
        position: absolute;
        width: 236px;
        cursor: pointer;
        height: 8px;
        background-color: rgb(128, 128, 128);
    }
    .SongInSiteProgressBar {
        background-color: rgb(153, 153, 153);
        width: 0;
        cursor: pointer;
        border-radius: 6px;
        height: 8px;
        position: relative;
        display: block;
    }
    .VolumeSignDiv {
        cursor: pointer;
        position: relative;
        display: inline-block;
        letter-spacing: -.36em;
    }
    .VolumeSignImg {
        top: -4px;
        position: relative;
        height: 24px;
        width: 30px;
        display:block;
    }