.createNewChatListButton {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 11px 0;
    margin-bottom: 5px;
    width: 262px;
    background-color: rgba(0, 0, 0, .1);
    border-radius: 5px;
    font-size: 20px;
    color: grey;
    cursor: pointer;
}
.createNewChatListMenu {
    $width: 400px;
    width: $width;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    background-color: #e3f3ff;
    .button {
        font-size: 18px;
        margin-right: 5px;
        padding: 5px;
        border-radius: 4px;
        cursor: pointer;
    }
}
.ListOfUsersForCreatingListOfChat {
    border-radius: 8px;
    border: 1px solid lightgrey;
    padding: 5px;
    width: 370px;
}
.ChatList {
    display: flex;
    width: 262px;
    padding: 5px 0;
    border-radius: 5px;
    margin-bottom: 5px;
    background-color: rgba(0, 0, 0, .1);
    flex-flow: column;
    .name {
        user-select: none;
        display: flex;
        width: 262px;
        padding: 0 10px;
        cursor: pointer;
        .text {
            outline: none;
            display: flex;
            align-items: center;
            width: 220px;
            height: 28px;
            font-size: 20px;
            overflow: hidden;
        }
    }
    .active {
        border-bottom: 2px rgb(25, 118, 210) solid;
        margin-bottom: 5px;
    }
}
