.ImageWindow {
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    z-index: 1010;
    .cross {
        display:block;
        top:45px;
        border-radius: 50%;
        background-color: rgb(0,0,0);
        right:calc(50vw - 22.5px);
        vertical-align: middle;
        opacity: 0.8;
        color:white;
        width: 45px;
        height: 45px;
        font-size: 32px;
        text-align: center;
        cursor:pointer;
        position:absolute;
    }
}